import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import StyledButton from "./Button";
import InputRow from "./InputRow";

const SignUpForm = ({
  name,
  id,
  password,
  email,
  passwordCheck,
  authNum,
  handleInput,
  onSubmitHandler,
  isValidPasswordCheck,
}) => {
  return (
    <>
      <SignUpContainer>
        <HeadText>   </HeadText>
        <SignUpFormBox onSubmit={onSubmitHandler}>
          <InputRow
            labelText=""
            type="name"
            value={name}
            onChange={handleInput("name")}
            placeholder=" "
            successComment="  "
          />
          <InputRow
            labelText=""
            type="email"
            value={email}
            onChange={handleInput("email")}
            placeholder=" "
            successComment="  "
          />
          <InputRow
            labelText=""
            type="authNum"
            value={authNum}
            onChange={handleInput("authNum")}
            placeholder=" "
            successComment=" "
          />
          <InputRow
            labelText=""
            type="id"
            value={id}
            onChange={handleInput("id")}
            placeholder=" "
            successComment="  "
          />
          <InputRow
            labelText=""
            type="password"
            value={password}
            onChange={handleInput("password")}
            placeholder=" "
            successComment="  "
          />
          <InputRow
            labelText=" "
            type="password"
            value={passwordCheck}
            onChange={handleInput("passwordCheck")}
            placeholder="   "
            successComment=" "
            failComment=" "
            isValid={isValidPasswordCheck}
          />
          <Link to="/profile">
            <ToProfilePageBtn type="submit">
                
            </ToProfilePageBtn>
          </Link>
        </SignUpFormBox>
      </SignUpContainer>
      ;
    </>
  );
};

export default SignUpForm;

const SignUpContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 460px;
  min-width: 400px;
  max-height: 1000px;
  margin: 50px auto;
  margin-bottom: 0;
  a {
    text-decoration: none;
  }
`;
const HeadText = styled.span`
  padding-bottom: 2rem;
  font-size: 1.3rem;
  font-weight: 600;
`;
const SignUpFormBox = styled.form`
  display: flex;
  flex-direction: column;
`;

const ToProfilePageBtn = styled(StyledButton)`
  width: 400px;
  height: 50px;
  margin-top: 1.5rem;
  color: #ffffff;
  font-size: 1rem;
  font-weight: 600;
`;
