import React from "react";
import styled from "styled-components";
import {
  HeadText,
  HeadTextContainer,
  InputForm,
  InputRowContainer,
  SlideContainer,
  ButtonContainer,
  PrevSlideButton,
  NextSlideButton,
} from "./SetNameJobSlide";

const representText = "  {'\n'}  ";
const addAnotherText = " {'\n'}  ";

const SetLocationSlide = ({ toggleSlide }) => {
  return (
    <>
      <SlideContainer>
        <HeadTextContainer>
          <HeadText> {"\n"}  </HeadText>
        </HeadTextContainer>
        <InputForm>
          <InputRowContainer>
            <LocationLabelText></LocationLabelText>
            <SubLocationLabelText>
              //     
              .
            </SubLocationLabelText>
          </InputRowContainer>
          <MapContainer></MapContainer>
        </InputForm>
        <ButtonContainer>
          <PrevSlideButton onClick={toggleSlide("prev")}>
            
          </PrevSlideButton>
          <NextSlideButton type="submit"></NextSlideButton>
        </ButtonContainer>
      </SlideContainer>
    </>
  );
};

export default SetLocationSlide;

const LocationLabelText = styled.div`
  margin-bottom: 10px;
  font-family: Pretendard;
  font-size: 16px;
  font-weight: 500;
  color: #8b8b8b;
`;

const SubLocationLabelText = styled.div`
  margin-bottom: 1rem;
  font-family: Pretendard;
  font-size: 13px;
  font-weight: 500;
  line-height: 21px;
  color: #8b8b8b;
  white-space: pre-wrap;
`;

const MapContainer = styled.div`
  display: flex;
  margin: 30px;
  border: none;
  border-radius: 16px;
  width: 400px;
  height: 400px;
  font-size: 1rem;
  color: #313338;
  background: #fafafa;
  .invisible {
    display: none;
  }
`;
